@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{height:100vh;background-color:#fafaff;font-family:"Recursive",sans-serif}.employee-counters{background-color:#eef0f2;padding:6rem 6rem;margin:4rem;border-radius:15px;display:flex;flex-wrap:wrap;justify-content:center;grid-gap:4rem;gap:4rem}
.header{padding:.5em 2em;color:#fff;font-size:1.4rem;background-color:#1c1c1c;text-align:center;display:flex;justify-content:space-between;align-items:center}
.card{width:250px;margin:auto;box-shadow:rgba(0,0,0,.3) 0px 19px 38px,rgba(0,0,0,.22) 0px 15px 12px;display:flex;flex-direction:column;justify-content:space-between;align-items:center;border-radius:15px;overflow:hidden;padding:0 1.6rem}
.employee{display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;padding:1rem}.employee__info{font-size:1.4em;font-weight:700;border-bottom:3px dashed #dd5211;padding:.4rem 3rem;margin:.6rem}.employee__detail{font-size:1.4rem;margin:0;margin-bottom:1rem}
.counter{width:100%;padding:20px 30px;background-color:#dd5211;color:#fafaff;display:flex;flex-direction:column}.counter__number{text-align:center;font-size:2.5rem;margin:0 0 .5rem 0}.counter__controls{display:flex;justify-content:center;grid-gap:10px;gap:10px}.counter__controls .counter__title{font-size:1.3rem;font-weight:600;color:#1c1c1c;margin:0}.counter__controls .counter__btn{background-color:transparent;border:none;font-size:1.2rem;cursor:pointer;color:#fafaff}.counter__controls .counter__btn:hover{color:#1c1c1c;transition:.2s}
